import DrawerInitiator from "../utils/drawer-initiator";
import UrlParser from "../routes/url-parser";
import routes from "../routes/routes";

class App {
  constructor({ button, drawer, content }) {
    this._button = button;
    this._drawer = drawer;
    this._content = content;

    this._initialAppShell();
  }

  async _initialAppShell() {
    DrawerInitiator.init({
      button: this._button,
      drawer: this._drawer,
      content: this._content,
    });

    // Render halaman utama (Home) saat aplikasi pertama kali dimuat
    await this.renderPage();
  }

  async renderPage() {
    const url = UrlParser.parseActiveUrlWithCombiner();
    const isLoggedIn = localStorage.getItem('isLoggedIn') === 'true';

    let page;

    if (!isLoggedIn) {
      // Jika belum login, hanya izinkan akses ke halaman Home dan Login
      if (url === '/' || url === '/login') {
        page = routes[url];
      } else {
        // Redirect ke Home jika akses halaman tidak diizinkan
        page = routes['/'];
      }
    } else {
      // Jika sudah login, izinkan akses ke semua halaman
      page = routes[url] || routes['/'];
    }

    if (page && typeof page.render === "function") {
      this._content.innerHTML = await page.render();
      await page.afterRender();
    } else {
      this._content.innerHTML = `<h2>Halaman tidak ditemukan</h2>`;
    }

    this._updateNavigationVisibility(isLoggedIn);
  }

  _updateNavigationVisibility(isLoggedIn) {
    const links = {
      agrowisata: document.getElementById('agrowisata-link'),
      desawisata: document.getElementById('desawisata-link'),
      aboutus: document.getElementById('aboutus-link'),
      login: document.getElementById('login-link'),
      accountInfo: document.getElementById('account-info'),
    };

    if (isLoggedIn) {
      links.agrowisata.style.display = 'block';
      links.desawisata.style.display = 'block';
      links.aboutus.style.display = 'block';
      links.login.style.display = 'none';
      links.accountInfo.style.display = 'block';
    } else {
      links.agrowisata.style.display = 'none';
      links.desawisata.style.display = 'none';
      links.aboutus.style.display = 'none';
      links.login.style.display = 'block';
      links.accountInfo.style.display = 'none';
    }
  }

  async handleLogin(event) {
    event.preventDefault();
    
    const username = document.getElementById('login-username').value;
    const password = document.getElementById('login-password').value;

    try {
      const response = await fetch('https://datausertravelkuy-448b9311f98b.herokuapp.com/login', {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify({ username, password }),
      });

      const data = await response.json();

      if (response.ok) {
        // Jika login berhasil, simpan status login
        localStorage.setItem('isLoggedIn', 'true');
        localStorage.setItem('username', username); // Simpan username jika perlu
        await this.renderPage(); // Panggil renderPage untuk memperbarui tampilan
      } else {
        // Jika login gagal, tampilkan pesan kesalahan
        alert(data.message || 'Username atau password salah');
      }
    } catch (error) {
      console.error('Error:', error);
      alert('Terjadi kesalahan saat melakukan login. Silakan coba lagi.');
    }
  }

  handleLogout() {
    localStorage.setItem('isLoggedIn', 'false');
    this.renderPage(); // Panggil renderPage untuk memperbarui tampilan setelah logout
  }

  initEventListeners() {
    document.getElementById('login-form').addEventListener('submit', (event) => this.handleLogin(event));
    document.getElementById('logout-button').addEventListener('click', () => this.handleLogout());
  }
}

// Inisialisasi aplikasi
const app = new App({
  button: document.getElementById('hamburgerButton'),
  drawer: document.getElementById('navigationDrawer'),
  content: document.getElementById('mainContent'),
});

// Inisialisasi event listeners
app.initEventListeners();

export default App;
