___CSS_LOADER_EXPORT___.push([module.id, `@media screen and (min-width: 600px) {
  .travelkuy {
    grid-template-columns: repeat(2, 1fr); /* Two columns on small tablets */
  }

  .travelkuy-item_judul {
    font-size: 1.75em; /* Increase font size for tablets */
  }

  .travelkuy-item_penjelasan {
    font-size: 1.125em; /* Slightly larger for tablets */
  }
}

@media screen and (min-width: 650px) {
 .app-bar {
    grid-template-columns: 1fr auto;
    padding: 8px 32px;
  }
 
  .app-bar .app-bar__brand h1 {
    font-size: 1.5em;
  }
 
  .app-bar .app-bar__menu {
    display: none;
  }
 
  .app-bar .app-bar__navigation {
    position: static;
    width: 100%;
  }
 
  .app-bar .app-bar__navigation ul li {
    display: inline-block;
  }
 
  .app-bar .app-bar__navigation ul li a {
    display: inline-block;
    width: 120px;
    text-align: center;
    margin: 0;
  }
 
  .wisata {
    grid-template-columns: 1fr 1fr;
  }
 
}
 
@media screen and (min-width: 800px) {
 .app-bar .app-bar__brand h1 {
    font-size: 2em;
  }
  
   .travelkuy {
    grid-template-columns: repeat(2, 1fr); /* Two columns on tablets */
    gap: 60px;
  }
}
 
@media screen and (min-width: 850px) {
  .wisata {
    grid-template-columns: repeat(3, 1fr);
  }
}

@media screen and (min-width: 900px) {
  .travelkuy {
    grid-template-columns: repeat(3, 1fr); /* Three columns on larger screens */
  }

  .travelkuy-item_judul {
    font-size: 2em; /* Increase font size for larger screens */
  }

  .travelkuy-item_penjelasan {
    font-size: 1.25em; /* Larger text for larger screens */
  }
}
 
@media screen and (min-width: 1200px) {
  .wisata {
    grid-template-columns: repeat(4, 1fr);
  }
  .travelkuy {
    grid-template-columns: repeat(3, 1fr); /* Three columns on larger screens */
    gap: 80px;
  }
  .travelkuy-item {
    flex-direction: row; /* Switch to horizontal layout on larger screens */
  }
  .travelkuy-item_gambar {
    width: 100%; /* Adjust image width for horizontal layout */
  }
  .travelkuy-item_isi {
    padding: 20px; /* Adjust padding for horizontal layout */
  }

}
 
@media screen and (min-width: 1600px) {
  .wisata {
      grid-template-columns: repeat(5, 1fr);
  }
}

@media (max-width: 600px) {
  .video-container {
    padding-bottom: 75%; /* Mengubah rasio menjadi 4:3 pada layar kecil */
  }
}

@media (max-width: 768px) {
  nav ul {
      flex-direction: column;
      align-items: flex-start;
  }

  nav li {
      margin: 10px 0;
  }
}

/* Responsive Design */
@media (max-width: 768px) {
  h1 {
      font-size: 28px;
  }

  .agrowisata-list {
      grid-template-columns: repeat(auto-fill, minmax(250px, 1fr));
  }

  #filter-location {
      width: 100%;
      margin: 10px 0;
  }
}

/* Responsive Design */
@media (max-width: 768px) {
  footer .footer-content {
      flex-direction: column;
      align-items: center;
  }

  footer .footer-section {
      margin: 10px 0;
      text-align: center;
      border-right: none; /* Hilangkan garis di layar kecil */
  }
}

/* DESA WISATA */
@media (max-width: 768px) {
  .desawisata-list {
    grid-template-columns: repeat(auto-fill, minmax(250px, 1fr));
  }

  .desawisata-item h2 {
    font-size: 1.4rem;
  }

  .desawisata-item p {
    font-size: 0.9rem;
  }

  .video-container iframe {
    height: 200px;
  }

  .detail-image {
    height: 250px;
  }

  iframe {
    height: 200px;
  }
}`, "",{"version":3,"sources":["webpack://./src/styles/responsive.css"],"names":[],"mappings":"AAAA;EACE;IACE,qCAAqC,EAAE,iCAAiC;EAC1E;;EAEA;IACE,iBAAiB,EAAE,mCAAmC;EACxD;;EAEA;IACE,kBAAkB,EAAE,gCAAgC;EACtD;AACF;;AAEA;CACC;IACG,+BAA+B;IAC/B,iBAAiB;EACnB;;EAEA;IACE,gBAAgB;EAClB;;EAEA;IACE,aAAa;EACf;;EAEA;IACE,gBAAgB;IAChB,WAAW;EACb;;EAEA;IACE,qBAAqB;EACvB;;EAEA;IACE,qBAAqB;IACrB,YAAY;IACZ,kBAAkB;IAClB,SAAS;EACX;;EAEA;IACE,8BAA8B;EAChC;;AAEF;;AAEA;CACC;IACG,cAAc;EAChB;;GAEC;IACC,qCAAqC,EAAE,2BAA2B;IAClE,SAAS;EACX;AACF;;AAEA;EACE;IACE,qCAAqC;EACvC;AACF;;AAEA;EACE;IACE,qCAAqC,EAAE,oCAAoC;EAC7E;;EAEA;IACE,cAAc,EAAE,0CAA0C;EAC5D;;EAEA;IACE,iBAAiB,EAAE,mCAAmC;EACxD;AACF;;AAEA;EACE;IACE,qCAAqC;EACvC;EACA;IACE,qCAAqC,EAAE,oCAAoC;IAC3E,SAAS;EACX;EACA;IACE,mBAAmB,EAAE,kDAAkD;EACzE;EACA;IACE,WAAW,EAAE,6CAA6C;EAC5D;EACA;IACE,aAAa,EAAE,yCAAyC;EAC1D;;AAEF;;AAEA;EACE;MACI,qCAAqC;EACzC;AACF;;AAEA;EACE;IACE,mBAAmB,EAAE,gDAAgD;EACvE;AACF;;AAEA;EACE;MACI,sBAAsB;MACtB,uBAAuB;EAC3B;;EAEA;MACI,cAAc;EAClB;AACF;;AAEA,sBAAsB;AACtB;EACE;MACI,eAAe;EACnB;;EAEA;MACI,4DAA4D;EAChE;;EAEA;MACI,WAAW;MACX,cAAc;EAClB;AACF;;AAEA,sBAAsB;AACtB;EACE;MACI,sBAAsB;MACtB,mBAAmB;EACvB;;EAEA;MACI,cAAc;MACd,kBAAkB;MAClB,kBAAkB,EAAE,mCAAmC;EAC3D;AACF;;AAEA,gBAAgB;AAChB;EACE;IACE,4DAA4D;EAC9D;;EAEA;IACE,iBAAiB;EACnB;;EAEA;IACE,iBAAiB;EACnB;;EAEA;IACE,aAAa;EACf;;EAEA;IACE,aAAa;EACf;;EAEA;IACE,aAAa;EACf;AACF","sourcesContent":["@media screen and (min-width: 600px) {\r\n  .travelkuy {\r\n    grid-template-columns: repeat(2, 1fr); /* Two columns on small tablets */\r\n  }\r\n\r\n  .travelkuy-item_judul {\r\n    font-size: 1.75em; /* Increase font size for tablets */\r\n  }\r\n\r\n  .travelkuy-item_penjelasan {\r\n    font-size: 1.125em; /* Slightly larger for tablets */\r\n  }\r\n}\r\n\r\n@media screen and (min-width: 650px) {\r\n .app-bar {\r\n    grid-template-columns: 1fr auto;\r\n    padding: 8px 32px;\r\n  }\r\n \r\n  .app-bar .app-bar__brand h1 {\r\n    font-size: 1.5em;\r\n  }\r\n \r\n  .app-bar .app-bar__menu {